.Modal {
    position: fixed;
    z-index: 500;
    background-color: white;
    width: 60%;
    border: 1px solid #ccc;
    box-shadow: 1px 1px 1px black;
    padding: 16px;
    left: 15%;
    top: 15%;
    box-sizing: border-box;
    transition: all 0.3s ease-out;
    border-radius:4px;
    max-height: 100%;
    overflow-y: scroll;
    margin-bottom: 50px;
}

@media (min-width: 600px) {
    .Modal {
        width: 500px;
        left: calc(50% - 250px);
    }
}